import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col } from 'antd';
import { CartHeadAction } from 'components/common';
import MenuTabTopClassHC from './MenuTabTopClassHC';
import FormInfoCommonClassHC from './FormInfoCommonClassHC';
import TableStatisClassHC from './TableStatisClassHC';
import './style.scss';

class StatisNumbersClassHCPage extends Component {
  state = {};
  render() {
    return (
      <Card title="Thống kê số lượng đăng ký">
        <CartHeadAction>
          <MenuTabTopClassHC />
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <FormInfoCommonClassHC />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableStatisClassHC />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <StatisNumbersClassHCPage />
  </Page>
);



