import React, { Component } from 'react';
import { Table } from 'antd';
import { setTableStatisClassHC } from 'dump/data';

class TableStatisCommon extends Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.columns = [
      {
        title: 'Tên lớp',
        dataIndex: 'tenLop',
        key: 'tenLop',
      },
      {
        title: 'Tên học phần',
        dataIndex: 'tenHocPhan',
        key: 'tenHocPhan',
      },
      {
        title: 'Tổng số SV',
        dataIndex: 'tongSoSV',
        key: 'tongSoSV',
      },
      {
        title: 'Tổng số SV đã ĐK',
        dataIndex: 'svDaDangKy',
        key: 'svDaDangKy',
      },
      {
        title: 'Tổng số SV chưa ĐK',
        dataIndex: 'svChuaDangKy',
        key: 'svChuaDangKy',
      },
      {
        title: 'Khóa học',
        dataIndex: 'khoaHoc',
        key: 'khoaHoc',
      },
      {
        title: 'Tên khoa',
        dataIndex: 'tenKhoa',
        key: 'tenKhoa',
      },
      {
        title: 'Chuyên ngành',
        dataIndex: 'chuyenNganh',
        key: 'chuyenNganh',
      },
    ];

    this.rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
  }
  render() {
    return (
      <Table
        bordered
        rowSelection={this.rowSelection}
        dataSource={setTableStatisClassHC}
        columns={this.columns}
      />
    );
  }
}

export default TableStatisCommon;



