import React, { Component } from 'react';
import { Menu, Icon, Form, Dropdown, Button } from 'antd';
import { Btn } from 'components/common/button';
import './style.scss';

const ButtonGroup = Button.Group;
@Form.create({ name: 'formMenuTabTopClassHC' })
class MenuTabTopClassHC extends Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.menu = (
      <Menu>
        <Menu.Item>
          <a
            target="_parent"
            rel="noopener noreferrer"
            href="#/students/StatisOfRegisNumbers/classHC"
          >
            TK số SV đăng ký theo lớp hành chính
          </a>
        </Menu.Item>
        <Menu.Item>
          <a target="_blank" rel="noopener noreferrer" href="#/">
            TK số SV đăng ký theo học phần
          </a>
        </Menu.Item>
        <Menu.Item>
          <a target="_blank" rel="noopener noreferrer" href="#/">
            TK số SV chưa đăng ký
          </a>
        </Menu.Item>
        <Menu.Item>
          <a target="_parent" rel="noopener noreferrer" href="#/students/StatisOfRegisNumbers">
            TK số SV đã đăng ký
          </a>
        </Menu.Item>
      </Menu>
    );
  }

  handleClick = e => {
    // console.log('click ', e);

    if (e.key === 'enteringInfo') {
      console.log('render');
    }
  };

  render() {
    return (
      <ButtonGroup>
        <Btn>
          <Dropdown overlay={this.menu}>
            <a href="#/">
              <Icon type="setting" /> Thống kê <Icon type="down" />
            </a>
          </Dropdown>
        </Btn>
        <Btn>
          <Icon type="swap" />
          Cập nhật trạng thái học lại và ngành 2
        </Btn>
        <Btn>
          <Icon type="printer" />
          In báo cáo
        </Btn>
        <Btn>
          <Icon type="file-excel" />
          Xuất Excel
        </Btn>
      </ButtonGroup>
    );
  }
}

export default MenuTabTopClassHC;



