import React, { Component } from 'react';
import { Row, Col, Form, Select, InputNumber, Input, Progress } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { schoolYearsTime, timesRegister } from 'dump/data';
import './style.scss';

@Form.create({ name: 'formClassList' })
class FormInfoCommonClassHC extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    const { Option } = Select;
    return (
      <Form layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} />
          <Col span={6}>
            <Form.Item label="Học kỳ:">
              <InputNumber min={1} max={3} defaultValue={1} style={{ width: '100%' }} />
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Năm học:">
              {getFieldDecorator('schoolYearsTime', {})(
                <Select>
                  {schoolYearsTime.map(item => (
                    <Option value={item.code}>{item.name}</Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Đợt:">
              {getFieldDecorator('timesRegister', {})(
                <Select>
                  {timesRegister.map(item => (
                    <Option value={item.code}>{item.name}</Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
        </Row>
        <br />
        <Row gutter={24}>
          <Col span={6}>
            <Form.Item label="Số TC đăng ký =<:">
              {getFieldDecorator('timesRegister', {})(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6} style={{ 'margin-top': '25px' }}>
            <Progress percent={30} />
          </Col>
        </Row>
        <br />
      </Form>
    );
  }
}

export default FormInfoCommonClassHC;



